<template>
  <div class="advertpage">
    <div class="over fr" @click="goHome">跳过</div>
    <div class="time fr">剩余{{num}}秒</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: 5,
      cleardata: null,
    };
  },
  methods: {
    goHome() {
      this.$router.push("/main/home"); 
    },
  },
  created() {
    this.cleardata = setInterval(() => {
      if (this.num == 0) {
        this.$router.push("/main/home"); 
      } else {
        this.num--;
      }
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.cleardata);
  },
};
</script>

<style lang="less" scoped>
.advertpage {
  background-image: url("../assets/images/advertpage.png");
  background-size: 100% 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  .time {
    margin: 20px 0;
    font-size: 16px;
    text-align: center;
    color: white;
  }
  .over {
    margin: 15px 15px;
    color: white;
    border: 1px solid white;
    font-size: 18px;
    padding: 2px 15px;
    border-radius: 50px;
  }
}
</style>